<template>
    <div>
        <van-nav-bar
            title="修改地址"
            left-arrow
            @click-left="$router.go(-1)"
        />
        <van-address-edit
            :area-list="areaList"
            show-search-result
            :search-result="searchResult"
            :address-info="addressInfo"
            :area-columns-placeholder="['请选择', '请选择', '请选择']"
            @save="onSave"
        />
    </div>
</template>

<script>
    import areaList from "../../assets/js/erea.js";
    export default {
        data() {
            return {
            areaList,
            searchResult: [],
            addressInfo:{}
            };
        },
        methods: {
            onSave({name,tel,province,city,county,areaCode,addressDetail}) {
                
                let id = this.$route.query.id;
                this.$store.commit("address/edit",{
                    id,
                    name,
                    tel,
                    address:province+city+county+addressDetail,
                    province,
                    city,
                    county,
                    areaCode,
                    addressDetail
                });
                this.$router.go(-1); 
            },
        },
        created(){
            let id = this.$route.query.id;
            this.addressInfo = this.$store.state.address.lists.find(item=>item.id == id); 

        }
    };
</script>